import axios from "axios";
import { useLayoutEffect, useState } from "react";
import { UserDataType } from "../../types";
import style from "./UserCard.module.scss";
import Button from "../Button/Button";
import { apiUrl } from "../../App";

export default function UserCard({ userId }: { userId: string }) {
  useLayoutEffect(() => {}, []);

  const initialUser = {
    id: "",
    name: "",
    email: "",
    phoneNumber: "",
    coins: 0,
  };

  const [user, setUser] = useState<UserDataType>(initialUser);

  useLayoutEffect(() => {
    const getUser = async (): Promise<void> => {
      try {
        axios
          .get(`${apiUrl}/consumers/${userId}`)
          .then((res) => setUser(res.data))
          .catch((err) =>
            console.log("    ", err)
          );
      } catch (error) {
        console.log(error);
      }
    };
    getUser();
  }, []);

  const deleteUser = async (): Promise<void> => {
    try {
      axios
        .delete(`${apiUrl}/consumers/${userId}`)
        // .then((res) => setUser(res.data))
        .catch((err) => console.log("  ", err));
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div className={style.card}>
      {user.name ? <span>:{user.name}</span> : <span>  </span>}
      {user.email ? (
        <span>:{user.email}</span>
      ) : (
        <span>  </span>
      )}
      {user.phoneNumber ? (
        <span> :{user.name}</span>
      ) : (
        <span>   </span>
      )}
      <span>: {user.coins} </span>
      <Button onClick={deleteUser} color="danger">
         
      </Button>
    </div>
  );
}
