import React, { ChangeEvent, useEffect, useState } from "react";
import Input from "../Input/Input";
import { ShortThingType } from "../../types";
import axios from "axios";
import Button from "../Button/Button";
import { notifySuccess, notifyWarning } from "../../toasters";
import style from "./AddThingForm.module.scss";
import { apiUrl } from "../../App";

export default function AddThingForm({
  setActive,
}: {
  setActive: React.Dispatch<React.SetStateAction<boolean>>;
}) {
  const initialThing = {
    name: "",
    description: "",
    color: "",
    size: "",
    count: 0,
    price: 0,
    category: "",
    brand: "",
  };

  const [input, setInput] = useState<ShortThingType>(initialThing);

  const changeHandler = (
    event: ChangeEvent<HTMLInputElement | HTMLSelectElement>
  ): void => {
    const { name, value } = event.target;

    setInput((prev) => ({
      ...prev,
      [name]: name === "count" || name === "price" ? Number(value) : value, //  count  price  
    }));
  };

  const addThing = async (): Promise<void> => {
    if (
      input.name &&
      input.description &&
      input.color &&
      input.size &&
      input.price > 0 &&
      input.count >= 0 &&
      input.category &&
      input.brand
    ) {
      try {
        const add = await axios.post(
          `${apiUrl}/products/`,
          input
        );
        notifySuccess("  .");
        setActive((prev) => !prev);
      } catch (error) {
        console.log(error);
      }
    } else {
      notifyWarning(",    .");
    }
  };

  return (
    <div className={style.form}>
      <label htmlFor="name">
         
        <Input
          type="text"
          name="name"
          onChange={changeHandler}
          value={input.name}
          placeholder=" "
        ></Input>
      </label>
      <label htmlFor="description">
         
        <Input
          type="text"
          name="description"
          onChange={changeHandler}
          value={input.description}
          placeholder=" "
        ></Input>
      </label>
      <label htmlFor="color">
         
        <Input
          type="text"
          name="color"
          onChange={changeHandler}
          value={input.color}
          placeholder=" "
        ></Input>
      </label>
      <label htmlFor="size" className={style.specialLabel}>
         
        <select name="size" onChange={changeHandler} value={input.size}>
          <option value=""> </option>
          <option value="ONE_SIZE">One Size</option>
          <option value="XXS">XXS</option>
          <option value="XS">XS</option>
          <option value="S">S</option>
          <option value="M">M</option>
          <option value="L">L</option>
          <option value="XL">XL</option>
          <option value="XXL">XXL</option>
          <option value="XXXL">XXXL</option>
          <option value="XXXXL">XXXXL</option>
        </select>
      </label>
      <label htmlFor="count">
         
        <Input
          type="number"
          name="count"
          onChange={changeHandler}
          value={input.count.toString()}
        ></Input>
      </label>
      <label htmlFor="price">
         
        <Input
          type="number"
          name="price"
          onChange={changeHandler}
          value={input.price.toString()}
        ></Input>
      </label>
      <label htmlFor="category">
         
        <Input
          type="string"
          name="category"
          onChange={changeHandler}
          value={input.category}
          placeholder=" "
        ></Input>
      </label>
      <label htmlFor="brand" className={style.specialLabel}>
        
        <select name="brand" onChange={changeHandler} value={input.brand}>
          <option disabled value="">
             
          </option>
          <option value="1 ">1 </option>
          <option value="1 ">1 </option>
          <option value="1 ">1 </option>
          <option value="1 ">1 </option>
          <option value="1 ">1 </option>
          <option value="1 ">1 </option>
          <option value="1  ">1  </option>
        </select>
      </label>
      <Button
        color="blue"
        onClick={() => {
          addThing();
        }}
      >
         
      </Button>
    </div>
  );
}
